


















































import { getTypeConversions, getTypes } from "@/api/apis/type.api";
import { rules } from "@/constants";
import Vue from "vue";
import TableHeader from "../core/TableHeader.vue";
import AddConvert from "@/components/type/AddConvert.vue";
import { TableHeaderModel } from "../models";
export default Vue.extend({
  components: { TableHeader, AddConvert },
  props: ["type"],
  data: () => ({
    rules: rules,
    search: "",
    isLoading: true,
    headers: [
      {
        text: "از نوع",
        align: "start",
        sortable: false,
        value: "fromType.name",
      },
      {
        text: "به نوع",
        align: "start",
        sortable: false,
        value: "toType.name",
      },
      {
        text: "از مقدار",
        align: "start",
        sortable: false,
        value: "fromValue",
      },
      {
        text: "به مقدار",
        align: "start",
        sortable: false,
        value: "toValue",
      },
      {
        text: "",
        align: "start",
        sortable: false,
        value: "actions",
      },
    ] as Array<TableHeaderModel>,
    converts: [] as Array<any>,
    newConvert: false,
  }),
  created() {
    this.loadConversions();
  },
  methods: {
    loadConversions() {
      this.isLoading = true;
      getTypeConversions(this.type.id)
        .then((convertRes) => {
          if (convertRes.status) this.converts = convertRes.result.conversions;
        })
        .finally(() => (this.isLoading = false));
    },
    addConvert() {
      this.newConvert = !this.newConvert;
    },
  },
});
